}

::placeholder {
  color: #666;
}

::selection {
  background-color: #D14270;
  color: #fff;
}

* {
  box-sizing: border-box;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

ol,
ul {
  list-style: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  color: inherit;
  text-decoration: none;
  opacity: .8;
}

strong,
b {
  font-weight: 600;
}

em {
  font-style: italic;
}

input {
  outline: none;
  font-size: 16px;
}

input[type=number] {
  -moz-appearance: textfield;
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input,
textarea {
  font-family: "Inter", "Georgia" "Arial", "san-serif";
  font-size: 1rem;
}

button,
input,
textarea {
  outline: none;
  border: none;
}

.d_none {
  display: none !important;
}

#overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(144, 154, 170, 0.5);
  z-index: 100;
}

body {
  font-size: 16px;
  height: 100%;
  width: 100%;
  position: relative;
  line-height: normal;
  font-style: normal;
  font-weight: normal;
  overflow-x: hidden;
  font-family: "Inter", "Georgia" "Arial", "san-serif";
  color: #2D395F;
  background-color: #fff;
  padding-top: 145px;
  padding-bottom: 50px;
}

body.dark_theme {
  color: #fff;
  background-color: #272F3A;
}

body.ov_hidden {
  overflow: hidden;
}

section,
header {
  height: auto;
  position: relative;
  z-index: 10;
}

.main_wrapper {
  width: 1235px;
  height: auto;
  margin: 0 auto;
  position: relative;
}

.bg_gray {
  background-color: #EBEFF7;
}

.title_line {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 40px;
}

.title_line h1,
.title_line h2 {
  margin-right: auto;
}

.title_line .btn {
  margin-left: auto;
}

body.dark_theme.bg_gray {
  background-color: #444E58;
}

h1,
.title_h1 {
  font-weight: bold;
  font-size: 50px;
  line-height: 55px;
}

.h1 {
  margin-top: 80px;
}

h2 {
  font-weight: bold;
  font-size: 40px;
  line-height: 45px;
}

h3 {
  font-weight: bold;
  font-size: 30px;
  line-height: 35px;
}

h4 {
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
}

.heading {
  text-align: center;
  font-size: 40px;
  line-height: 45px;
  font-weight: bold;
}

.lang_switcher {
  width: 50px;
  position: relative;
  cursor: pointer;
  z-index: 2;
}

.lang_switcher.opened .lang_hidden {
  display: block;
  background-color: #fff;
}

.lang_switcher .lang {
  width: fit-content;
  height: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: auto;
}

.lang_switcher .lang span {
  margin-right: 6px;
  font-size: 15px;
  line-height: 20px;
  letter-spacing: -0.01em;
}

.lang_switcher .lang svg {
  width: 20px;
  height: 20px;
}

.lang_switcher .lang_visible {
  width: 100%;
  height: 20px;
  padding: 0 5px;
}

.lang_switcher .lang_hidden {
  width: 100%;
  position: absolute;
  top: 100%;
  left: 0;
  padding: 5px 5px 10px;
  background-color: #fff;
  display: none;
}

.lang_switcher .lang {
  margin-bottom: 5px;
}

.lang_switcher .lang:hover {
  opacity: .8;
}

.lang_switcher .lang:last-child {
  margin-bottom: 0;
}

body.dark_theme .lang_switcher.opened .lang_hidden {
  background-color: #272F3A;
}

input.btn:disabled,
button.btn:disabled {
  cursor: default;
  background-color: #EBEFF7;
  color: #D3D2DA;
}

.btn {
  width: 245px;
  height: 60px;
  background: #D14270;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  line-height: 30px;
  letter-spacing: -0.01em;
  color: #fff;
  cursor: pointer;
}

.btn:hover {
  color: #fff;
  opacity: .9;
}

.btn.disabled {
  cursor: default;
  background-color: #EBEFF7;
  color: #D3D2DA;
}

.btn.to_write {
  width: 132px;
  height: 40px;
}

.btn_login {
  width: 120px;
  height: 40px;
  border: 1px solid #2D395F;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  line-height: 30px;
  letter-spacing: -0.01em;
  text-transform: uppercase;
  color: #2D395F;
}

.btn_login:hover {
  color: #2D395F;
}

.back_button {
  font-size: 15px;
  line-height: 35px;
  letter-spacing: -0.01em;
  color: #7A84A6;
  margin-bottom: 5px;
}

.bg_gray .btn.disabled {
  border: 1px solid #fff;
}

.bg_gray button.btn:disabled {
  border: 1px solid #fff;
}

body.dark_theme .btn_login {
  border: 1px solid #fff;
  color: #fff;
}

.document_item {
  width: 49%;
  height: auto;
  margin-right: 2%;
  margin-bottom: 25px;
  position: relative;
  padding-left: 50px;
}

.document_item:nth-child(2n) {
  margin-right: 0;
}

.document_item .type {
  width: 40px;
  height: 25px;
  border-radius: 5px;
  border: 1px solid #D14270;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  color: #D14270;
  font-size: 12px;
  position: absolute;
  top: 2px;
  left: 0;
}

.document_item .doc_title {
  font-size: 20px;
  line-height: 30px;
  letter-spacing: -0.01em;
}

.contacts_popup_wrapper {
  width: 100%;
  height: 100vh;
  z-index: 9999;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contacts_popup_wrapper .contacts_popup {
  width: 1366px;
  height: auto;
  padding: 4vw;
  position: relative;
  background-color: #fff;
}

.contacts_popup_wrapper .contacts_popup .close_contacts_popup {
  width: 40px;
  height: 40px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 35px;
  right: 35px;
}

.contacts_popup_wrapper .contacts_popup .close_contacts_popup svg {
  width: 30px;
  height: 30px;
}

.contacts_popup_wrapper .contacts_popup .form_wrapper {
  width: 100%;
  height: auto;
}

.contacts_popup_wrapper .contacts_popup .form_wrapper h2 {
  margin-bottom: 50px;
}

.contacts_popup_wrapper .contacts_popup .form_wrapper .contacts_popup_form {
  width: 65%;
  height: auto;
}

.contacts_popup_wrapper .contacts_popup .form_wrapper .contacts_popup_form textarea {
  width: 100%;
  height: 180px;
  border: 1px solid #7A84A6;
  padding: 10px 15px;
  resize: none;
  border-radius: 5px;
  margin-bottom: 40px;
}

.contacts_popup_wrapper .contacts_popup .form_wrapper .contacts_popup_form textarea::placeholder {
  color: #2D395F;
}

.contacts_popup_wrapper .contacts_popup .form_wrapper .contacts_popup_form .title {
  font-weight: bold;
  font-size: 30px;
  line-height: 35px;
  color: #2D395F;
  margin-bottom: 20px;
}

.contacts_popup_wrapper .contacts_popup .form_wrapper .contacts_popup_form .file_field_block {
  width: 100%;
  height: auto;
  position: relative;
  margin-bottom: 40px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
}

.contacts_popup_wrapper .contacts_popup .form_wrapper .contacts_popup_form .file_field_block label {
  width: fit-content;
  height: auto;
  font-size: 20px;
  line-height: 30px;
  letter-spacing: -0.01em;
  text-decoration-line: underline;
  cursor: pointer;
  margin-right: 20px;
}

.contacts_popup_wrapper .contacts_popup .form_wrapper .contacts_popup_form .file_field_block .file_field {
  width: 0;
  height: 0;
  overflow: hidden;
}

.contacts_popup_wrapper .contacts_popup .alert_done {
  width: 100%;
  height: auto;
  font-size: 25px;
  color: green;
  text-align: center;
}

.pagination {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.pagination .pg {
  height: 30px;
  min-width: 30px;
  width: fit-content;
  padding: 0 10px;
  background-color: #D14270;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 5px;
}

.pagination .pg:last-child {
  margin-right: 0;
}

.pagination .pg svg {
  width: 15px;
  height: 15px;
}

.pagination .pg svg path {
  fill: #fff;
}

.pagination .pg.currient {
  background-color: #7A84A6;
  color: #EBEFF7;
}

.pagination .pg.currient:hover {
  opacity: 1;
}

.pagination .pg.first {
  margin-right: 15px;
}

.pagination .pg.last {
  margin-left: 10px;
}

.callback_item {
  width: 49%;
  margin-right: 2%;
  height: auto;
  padding: 30px;
  border-radius: 10px;
  background-color: #EBEFF7;
  margin-bottom: 25px;
}

.callback_item:nth-child(2n) {
  margin-right: 0;
}

.callback_item .name {
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
  color: #2D395F;
  margin-bottom: 5px;
}

.callback_item .date {
  font-size: 15px;
  line-height: 20px;
  letter-spacing: -0.01em;
  color: #A3A3A3;
  margin-bottom: 20px;
}

.callback_item .content {
  font-size: 20px;
  line-height: 30px;
  letter-spacing: -0.01em;
  color: #2D395F;
}

.callback_item .show_full {
  color: #7A84A6;
  font-size: 15px;
  text-decoration: underline;
  cursor: pointer;
  margin-top: 30px;
}

.callback_item .show_full:hover {
  opacity: .9;
}

.callback_item .double_block {
  width: 100%;
  height: auto;
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.callback_item .double_block .double_block_item {
  width: 48%;
  height: auto;
  font-size: 15px;
  line-height: 20px;
  letter-spacing: -0.01em;
}

.callback_item .double_block .double_block_item .db_title {
  width: 100%;
  height: auto;
}

.callback_item .double_block .double_block_item .db_content {
  width: 100%;
  height: auto;
  color: #7A84A6;
}

.content_block {
  width: 100%;
  height: auto;
  margin-bottom: 80px;
}

.content_block p {
  width: 100%;
  height: auto;
  margin-bottom: 40px;
  font-size: 20px;
  line-height: 30px;
}

.content_block p:last-child {
  margin-bottom: 0;
}

.content_block img {
  width: 100%;
  height: auto;
  display: block;
  margin-bottom: 40px;
}

.content_block h1,
.content_block h2,
.content_block h3 {
  margin-top: 80px;
  margin-bottom: 20px;
}

.content_block h1:first-child,
.content_block h2:first-child,
.content_block h3:first-child {
  margin-top: 0;
}

.content_block_slider_container {
  width: 100%;
  height: auto;
}

.content_block_slider_container .content_block_slider {
  width: 100%;
  height: auto;
}

.content_block_slider_container .content_block_slider .content_block_slider_slide {
  height: auto;
  position: relative;
}

.content_block_slider_container .content_block_slider .content_block_slider_slide .image_wrapper {
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #7A84A6;
  margin-bottom: 10px;
}

.content_block_slider_container .content_block_slider .content_block_slider_slide .image_wrapper img {
  width: auto;
  height: auto;
  max-width: 80%;
  max-height: 80%;
}

.content_block_slider_container .content_block_slider .content_block_slider_slide .title {
  font-size: 15px;
  line-height: 20px;
  letter-spacing: -0.01em;
  color: #2D395F;
}

.show_more_slider_container {
  width: 100%;
  height: auto;
}

.show_more_slider_container .show_more_slider {
  width: 100%;
  height: auto;
}

.show_more_slider_container .show_more_slider .show_more_slider_slide {
  background: #EDF1FA;
  border-radius: 10px;
  padding: 35px 35px 70px;
  height: auto;
  position: relative;
}

.show_more_slider_container .show_more_slider .show_more_slider_slide .top_line {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 30px;
}

.show_more_slider_container .show_more_slider .show_more_slider_slide .top_line .category {
  margin-right: auto;
  padding: 5px 15px;
  background: #7A84A6;
  border-radius: 3px;
  width: auto;
  height: auto;
  color: #fff;
}

.show_more_slider_container .show_more_slider .show_more_slider_slide .top_line .date {
  margin-left: auto;
  font-size: 15px;
  line-height: 20px;
  letter-spacing: -0.01em;
  color: #A3A3A3;
}

.show_more_slider_container .show_more_slider .show_more_slider_slide .content {
  font-size: 20px;
  line-height: 30px;
  letter-spacing: -0.01em;
  color: #2D395F;
  margin-bottom: 30px;
}

.show_more_slider_container .show_more_slider .show_more_slider_slide .show_more_link {
  position: absolute;
  left: 35px;
  bottom: 35px;
  font-size: 20px;
  line-height: 30px;
  letter-spacing: -0.01em;
  color: #7A84A6;
  cursor: pointer;
}

.show_more_slider_container .show_more_slider .callback_slide {
  height: auto;
  background: #EDF1FA;
  border-radius: 10px;
  padding: 30px 30px;
  position: relative;
}

.show_more_slider_container .show_more_slider .callback_slide .cb_title {
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
  color: #2D395F;
}

.show_more_slider_container .show_more_slider .callback_slide .cb_date {
  font-size: 15px;
  line-height: 20px;
  letter-spacing: -0.01em;
  color: #A3A3A3;
  margin-bottom: 20px;
}

.show_more_slider_container .show_more_slider .callback_slide .cb_content {
  font-size: 20px;
  line-height: 30px;
  letter-spacing: -0.01em;
  color: #2D395F;
}

.header_nav {
  width: 100%;
  height: auto;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 50;
}

.header_nav.sticked {
  background-color: #fff;
}

.header_nav .main_nav_top,
.header_nav .main_nav_bottom {
  width: 100%;
  background-color: #fff;
}

.header_nav .main_nav_top .container-lg,
.header_nav .main_nav_bottom .container-lg {
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
}

.header_nav .main_nav_top .submeu_moby,
.header_nav .main_nav_bottom .submeu_moby {
  display: none;
  margin-bottom: 35px;
}

.header_nav .main_nav_top .submeu_moby .link_block,
.header_nav .main_nav_bottom .submeu_moby .link_block {
  width: 100%;
  height: auto;
  margin-bottom: 35px;
}

.header_nav .main_nav_top .submeu_moby .link_block:last-child,
.header_nav .main_nav_bottom .submeu_moby .link_block:last-child {
  margin-bottom: 0;
}

.header_nav .main_nav_top .submeu_moby .link_block .menu_title,
.header_nav .main_nav_bottom .submeu_moby .link_block .menu_title {
  width: fit-content;
  font-weight: bold;
  font-size: 25px;
